workbox_routing_registerRoute(/^https:\/\/.*\.(js|css)$/, new workbox_strategies_StaleWhileRevalidate({ "cacheName":"static-resources-cache", plugins: [new workbox_expiration_ExpirationPlugin({ maxEntries: 100, maxAgeSeconds: 604800 })] }), 'GET');




